fault;\r\n\r\n$input-focus-bg: $input-bg !default;\r\n$input-focus-border-color: lighten($primary, 25%) !default;\r\n$input-focus-width: 0.2rem !default;\r\n$input-focus-color: $input-color !default;\r\n$input-focus-shadow-color: rgba($primary, 0.25) !default;\r\n$input-focus-box-shadow: 0 0 0 $input-focus-width $input-focus-shadow-color !default;\r\n\r\n$input-placeholder-color: $gray-600 !default;\r\n$input-plaintext-color: $body-color !default;\r\n\r\n$input-height-border: $input-border-width * 2 !default;\r\n\r\n$input-transition: border-color 0.3s ease-in-out, box-shadow 0.3s ease-in-out !default;\r\n\r\n$input-group-addon-color: $input-color !default;\r\n$input-group-addon-bg: $gray-200 !default;\r\n$input-group-addon-border-color: $input-border-color !default;\r\n\r\n// Progress bars\r\n$progress-font-size: $font-size-base * 0.75 !default;\r\n$progress-bg: $gray-200 !default;\r\n$progress-border-radius: $border-radius !default;\r\n$progress-bar-color: $white !default;\r\n$progress-bar-transition: width 0.6s ease !default;","@mixin button-size($padding-y, $padding-x, $font-size, $border-radius) {\r\n  padding: $padding-y $padding-x;\r\n  font-size: $font-size;\r\n  border-radius: $border-radius;\r\n}\r\n\r\n@mixin button-style($background,\r\n  $border,\r\n  $color,\r\n  $hover-background: lighten($background, 7.5%),\r\n  $hover-border: lighten($border, 10%),\r\n  $hover-color: $color) {\r\n  color: $color;\r\n  background: $background;\r\n  border-color: $border;\r\n\r\n  &:hover {\r\n    color: $hover-color;\r\n    background: $hover-background;\r\n    border-color: $hover-border;\r\n  }\r\n\r\n  &:focus,\r\n  &.focus {\r\n    // outline: none;\r\n    color: $hover-color;\r\n    background: $hover-background;\r\n    border-color: $hover-border;\r\n  }\r\n\r\n  &:disabled,\r\n  &.disabled {\r\n    color: $color;\r\n    background: $background;\r\n    border-color: $border;\r\n  }\r\n}\r\n\r\n@mixin zoom-animation($direction: 'top', $scaleStart: scaleY(0), $scaleEnd: scaleY(1), $origin: center top) {\r\n  .zoom-in-#{$direction}-enter {\r\n    opacity: 0;\r\n    transform: $scaleStart;\r\n  }\r\n\r\n  .zoom-in-#{$direction}-enter-active {\r\n    opacity: 1;\r\n    transform: $scaleEnd;\r\n    transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1) 100ms, opacity 300ms cubic-bezier(0.23, 1, 0.32, 1) 100ms;\r\n    transform-origin: $origin;\r\n  }\r\n\r\n  .zoom-in-#{$direction}-exit {\r\n    opacity: 1;\r\n  }\r\n\r\n  .zoom-in-#{$direction}-exit-active {\r\n    opacity: 0;\r\n    transform: $scaleStart;\r\n    transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1) 100ms, opacity 300ms cubic-bezier(0.23, 1, 0.32, 1) 100ms;\r\n    transform-origin: $origin;\r\n  }\r\n}\r\n\r\n@mixin border-right-radius($raduis) {\r\n  border-top-right-radius: $raduis;\r\n  border-bottom-right-radius: $raduis;\r\n}\r\n\r\n@mixin border-left-radius($raduis) {\r\n  border-top-left-radius: $raduis;\r\n  border-bottom-left-radius: $raduis;\r\n}\r\n\r\n@mixin alert-color($background, $color) {\r\n  color: $color;\r\n  background-color: $background;\r\n}","@include zoom-animation();\r\n@include zoom-animation('left', scale(0.45, 0.45), scale(1, 1), top left);\r\n@include zoom-animation('right', scale(0.45, 0.45), scale(1, 1), top right);\r\n@include zoom-animation('bottom', scaleY(0), scaleY(1), center bottom);",".btn {\r\n  position: relative;\r\n  display: inline-block;\r\n  font-weight: $btn-font-weight;\r\n  line-height: $btn-line-height;\r\n  color: $body-color;\r\n  white-space: nowrap;\r\n  text-align: center;\r\n  vertical-align: middle;\r\n  background-image: none;\r\n  border: $btn-border-width solid transparent;\r\n  @include button-size($btn-padding-y, $btn-padding-x, $btn-font-size, $border-radius);\r\n  box-shadow: $btn-box-shadow;\r\n  cursor: pointer;\r\n  transition: $btn-transition;\r\n\r\n  &.disabled,\r\n  &[disabled] {\r\n    cursor: not-allowed;\r\n    opacity: $btn-disabled-opacity;\r\n    box-shadow: none;\r\n\r\n   